document.addEventListener('DOMContentLoaded', function() {
    crearGaleriaIndex();
})

function crearGaleriaIndex() {

    const galeria = document.querySelector('.cert-bg__index')

    for(let i = 3; i >= 1; i--) {
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/Cert-thumb/${i}.webp`;
        imagen.setAttribute("alt", "Certificado");
        imagen.classList.add('certificado__img');
        imagen.dataset.imagenId = i;

        // Añadir la funcion de mostrarImagen
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('DIV');
        lista.appendChild(imagen);
        lista.classList.add('certificado__index')

        galeria.appendChild(lista);
    }
}